import { SelectionModel } from '@angular/cdk/collections';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatMenuTrigger } from '@angular/material/menu';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { ExpenseManagementService } from 'src/app/erp-finance/data/services/expense-management.service';
import { ManageRfqsComponent } from 'src/app/erp-procurement/modules/rfqs/pages/manage-rfqs/manage-rfqs.component';

import { SnackbarService } from 'src/app/shared/services/snackbar.service';

@Component({
  selector: 'app-expense-lookup',
  templateUrl: './expense-lookup.component.html',
  styleUrls: ['./expense-lookup.component.sass']
})
export class ExpenseLookupComponent implements OnInit {

  displayedColumns: string[] = [
    "select",
    "id",
    "expenseCode",
   // "expenseType",
    "expenseName",
   // "expenseGlAccount",
  ];

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  contextMenu: MatMenuTrigger;

  selection = new SelectionModel<any>(false, []);

  expenseDetails: any;
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;

  dataSourceFilteredList: any[] = [];
  expenseArray: any[] = [];

    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  isLoading: boolean = true;

  constructor(
    public dialogRef: MatDialogRef<ManageRfqsComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private expsenseManService: ExpenseManagementService,
  ) { }

  ngOnInit(): void {
    if(this.data.action === 'Single Expense'){
      this.selection = new SelectionModel<any>(false, []);
    }else{
      this.selection = new SelectionModel<any>(true, []);
    }
    
    this.getData();
  }
  ngAfterViewInit() {
    console.log("Finally: ", this.dataSourceFilteredList);
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  filter() {
    //let storeId = [1, 2, 3];
    this.dataSource.data.forEach((element) => {
      this.expenseArray.forEach((item) => {
        if (item === element.id) {
          // this.dataSourceFilteredList.push(this.dataSource.data.indexOf(element));
          this.selection.select(element);
        }
      });
    });

    console.log("dataSourceFilteredList ", this.dataSourceFilteredList);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  //******************************************************************************************************
  //Select expense

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }
  proceed() {
    this.dialogRef.close({ event: "close", data: this.selectedRows });

    //   console.log(data);
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.selectedRows });
  }
  public confirmAdd(): void { }

  getData() {
    let params = { status: "APPROVED" }
    this.expsenseManService
      .getExpensesByStatus(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {

          console.log("RESPONSE =>: ", res);


          if (res.statusCode === 200) {
            this.data = res.entity;

            this.expenseDetails = res.entity;

            this.expenseArray = this.data.selected;

            console.log("this.currencies: ", this.expenseDetails);

            this.dataSource = new MatTableDataSource<any>(this.expenseDetails);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          }
          else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }



          this.isLoading = false;
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => { },
      }),
      Subscription;
  }
}
